<template>
  <div class="loginView">
    <form class="bd">
      <ul>
        <li><span>账号:</span><input type="text" v-model="new_user_name" /></li>
        <li>
          <span>密码:</span><input type="password" v-model="new_user_pasword" />
        </li>
        <li>
          <span>确认密码:</span
          ><input type="password" v-model="pasword_enter" />
        </li>
        <li>{{ tips }}</li>
        <li><div class="send" @click="sendE">确认注册</div></li>
      </ul>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      new_user_name: "",
      new_user_pasword: "",
      pasword_enter: "",
      tips: "",
    };
  },
  beforeMount() {
    this.sendE();
  },
  methods: {
    sendE() {
      if (this.new_user_pasword === this.pasword_enter) {
        this.tips = " ";
        let new_user_date = {
          name: this.new_user_name,
          psw: this.new_user_pasword,
        };
        this.$store.commit("GETREGISTER", [new_user_date, this]);
        this.$bus.$on("same", (data) => {
          if (!data) {
            this.tips = "该账号已存在";
          } else {
            this.$router.push({
              name: "login",
            });
          }
        });
      } else {
        this.tips = "两次输入的密码不相同";
      }
    },
  },
};
</script>

<style scoped>
.loginView {
  width: 100%;
  height: 100vh;
  background-color: rgb(245, 162, 162);
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 345px;
}
.bd {
  box-sizing: border-box;
  padding: 50px;
  border: 2px solid white;
  border-radius: 13px;
}
.bd li {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  height: 65px;
}
.bd li span {
  display: block;
  font: 700 1rem "";
  width: 27%;
}
.bd input {
  outline: none;
  border: 0;
  height: 40px;
  margin-left: 10px;
  border-radius: 20px;
  box-sizing: border-box;
  padding: 10px;
}
.bd .send {
  box-sizing: border-box;
  padding: 10px;
  border: 1px solid white;
  margin-top: 25px;
  border-radius: 12px;
  cursor: pointer;
  user-select: none;
}
</style>
